.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.table{
  border-collapse: separate;
  border-spacing: 0 1em;
}
.tableContainer{
  max-height: 25em;
  max-width: 100%;
  overflow-x: hidden;
  padding: 1em;
}

.tableContainer:hover {
  overflow-y: scroll;
}

.tableContainer::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.tableContainer::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

.tableContainer::-webkit-scrollbar-thumb
{
	background-color: #000000;
	border: 2px solid #555555;
}


.class_issue{
  width: 8em;
}
.class_timeSpent{
  width: 6em;
}
.class_comments, .class_logDate{
  width: 10em;
}
.class_search{
  width: 74%;
}

.class_issue, .class_timeSpent, .class_comments, .class_search, .class_logDate{
  height: 1.5em;
  text-align: center;
  padding: 4px 3px 2px 4px;
  border-radius: 6px;
  outline: none;
}
.btn-save{
  width: 98%;
  height: 2.6em;
  text-align: center;
  border-radius: 6px;
  outline: none;
  background: green;
  color: azure;
  border: 0;
  cursor: pointer;
  margin-bottom: 1rem;
}
.btn-add{
  width: 8em;
  height: 2.3em;
  text-align: center;
  border-radius: 6px;
  outline: none;
  background: black;
  color: azure;
  border: 0;
  margin-left: 4px;
  cursor: pointer;
  border-radius: 4px;
}
.del-btn{
  width: 2em;
  height: 2.3em;
  text-align: center;
  border-radius: 9px;
  outline: none;
  background: orangered;
  color: azure;
  border: 0;
  cursor: pointer;
}

.searchOrAdd{
  margin-top: 1em;
  margin-bottom: 1em;
}
.autoCommentContainer{
  text-align: left;
  padding: 0 1em;
}
.autoCommentContainer > input {
  margin: 1px 4px;
}
.autoCommentContainer > label {
  vertical-align: top;
}

button, .del-btn {
  box-shadow: 2px 1px 2px gray;
}

button:active, .del-btn:active {
  box-shadow: 0 0 0 white;
}

.logDetails {
  text-align: left;
  padding: 1em;
  max-height: 15em;
}

.logDetails:hover {
  overflow-y: scroll;
}

.logDetails::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
}

.logDetails::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

.logDetails::-webkit-scrollbar-thumb
{
	background-color: #000000;
	border: 2px solid #555555;
}
.logRow{
  display: flex;
  flex-direction: row;
  align-content: space-between;
  align-items: center;
  margin: 1em 0;
}
.logDelBtn {
  border: none;
  font-size: 10px;
  border-radius: 5px;
  background: orangered;
  color: azure;
  height: 2em;
  cursor: pointer;
}